/* reset */
* {
  padding: 0; margin: 0;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}

/* 레이아웃 */

.wrap {
  width: 1120px;
  margin: 0 auto;
}
.navbar {
  height: 80px;
}
.main {
  height: 700px;
  background-color: #666;
}
.footer {
  background-color: #000;
  color: #fff
}

/* 네브 */
.nav-menu ul {
  display: flex;
  justify-content: space-between;
}
.nav-menu li a {
  color: #000;
  font-size: 17px;
  font-weight: bold;
  line-height: 80px;
  padding: 20px;
}

/* 메인 */


/* 푸터 */
.footer .footer-widgets {
  padding: 20px 0 20px 0;
  text-align: center;
}
.footer .social-links {
  margin: 0 auto;
  width: 50%;
  display: flex;
  justify-content: space-between;
}
.footer .social-links li {

}
.footer .social-links li a {
  color: #fff;
}
.site-infos {
  text-align: center;
  font-size: 12px;
  padding: 0 0 20px 0;
}